.App {
  display: flex;
  justify-content: center;
  flex-direction: column;

  width: 100%;
}

.App__title {
  font-size: 3rem;
  font-weight: bold;

  display: flex;
  justify-content: center;

  text-decoration: underline 2px solid navy;
}

.App__container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  justify-content: center;
}

.text--winning {
  font-size: 2rem;
  font-weight: bold;

  color: navy;
  text-align: center;
}

.text--order {
  font-size: 2rem;
  font-weight: bold;

  color: red;
  text-align: center;
}

.button--reset {
  width: 6rem;
  height: 2rem;

  margin: 0 auto;
  font-size: 1rem;
}
